import { NextResponse } from "next/server";
import type { NextRequest } from "next/server";
import { getToken } from "next-auth/jwt";

// Rutas pblicas (no pedir sesin)
const PUBLIC = [
  /^\/$/,                      // home
  /^\/login(?:\/|$)/,
  /^\/signup(?:\/|$)/,         // pgina de alta pblica
  /^\/pending-approval(?:\/|$)/,
  /^\/redirect(?:\/|$)/,
  /^\/api\/auth(?:\/|$)/,
  /^\/api\/users(?:\/|$)/,     //  API de signup PBLICA
  /^\/_next\/static(?:\/|$)/,
  /^\/favicon\.ico$/,
];

// Guard CT / API CT
const CT_PATHS = [
  /^\/ct(?:\/|$)/,
  /^\/api\/ct(?:\/|$)/,
  /^\/api\/sessions(?:\/|$)/,
  //  /api/users ya NO va ac (es pblico)
];

// Guard Mdico / API Mdico
const MED_PATHS = [
  /^\/medico(?:\/|$)/,
  /^\/api\/medico(?:\/|$)/,
];

// Excepcin: CT puede LEER endpoints clnicos
function isClinicalReadForCT(pathname: string, method: string) {
  if (method !== "GET") return false;
  return /^\/api\/medico\/clinical(?:\/|$)/.test(pathname);
}

function matchAny(pathname: string, patterns: RegExp[]) {
  return patterns.some((r) => r.test(pathname));
}

function roleHome(role?: string) {
  switch (role) {
    case "ADMIN": return "/admin";
    case "CT": return "/ct";
    case "MEDICO": return "/medico";
    case "JUGADOR": return "/jugador";
    case "DIRECTIVO": return "/directivo";
    default: return "/login";
  }
}

export async function middleware(req: NextRequest) {
  const { pathname } = req.nextUrl;
  const isAPI = pathname.startsWith("/api");

  // Pblicos -> dejar pasar
  if (matchAny(pathname, PUBLIC)) {
    return NextResponse.next();
  }

  // Qu guard aplica?
  const needsCT = matchAny(pathname, CT_PATHS);
  const needsMED = matchAny(pathname, MED_PATHS);

  // Si no matchea nada, dejar pasar
  if (!needsCT && !needsMED) {
    return NextResponse.next();
  }

  const token = await getToken({ req, secret: process.env.NEXTAUTH_SECRET });

  if (!token) {
    if (isAPI) {
      return new NextResponse(JSON.stringify({ error: "Unauthorized" }), {
        status: 401,
        headers: { "content-type": "application/json" },
      });
    }
    const url = req.nextUrl.clone();
    url.pathname = "/login";
    url.searchParams.set("callbackUrl", pathname);
    return NextResponse.redirect(url);
  }
  // Bypass global para SUPERADMIN: controla todo sin restricciones de equipo.
  const role = (token as any).role as string | undefined;
  if (role === "SUPERADMIN") {
    return NextResponse.next();
  }

  const isApproved = (token as any).isApproved as boolean | undefined;

  // Gate global: si no est aprobado y NO es admin  pending
  if (role !== "ADMIN" && isApproved === false) {
    if (isAPI) {
      return new NextResponse(JSON.stringify({ error: "Forbidden", pendingApproval: true }), {
        status: 403,
        headers: { "content-type": "application/json" },
      });
    }
    const url = req.nextUrl.clone();
    url.pathname = "/pending-approval";
    return NextResponse.redirect(url);
  }

  // Guard CT
  if (needsCT) {
    const allowed = role === "CT" || role === "ADMIN";
    if (!allowed) {
      console.info("[middleware] deny", {
        reason: "role-mismatch",
        pathname,
        guard: "CT",
        role,
        userId: token.sub,
      });
      const url = req.nextUrl.clone();
      url.pathname = roleHome(role);
      return NextResponse.redirect(url);
    }
  }

  // Guard Mdico (con excepcin de lectura para CT)
  if (needsMED) {
    const allowCTReadOnly = role === "CT" && isClinicalReadForCT(pathname, req.method);
    const allowed = role === "MEDICO" || role === "ADMIN" || allowCTReadOnly;
    if (!allowed) {
      console.info("[middleware] deny", {
        reason: "role-mismatch",
        pathname,
        guard: "MEDICO",
        role,
        userId: token.sub,
      });
      const url = req.nextUrl.clone();
      url.pathname = roleHome(role);
      return NextResponse.redirect(url);
    }
  }

  const requiresTeam = needsCT || needsMED;
  if (requiresTeam) {
    const cookieTeam = req.cookies.get("ctb_team")?.value?.trim();
    const tokenTeam =
      typeof (token as any).currentTeamId === "string"
        ? ((token as any).currentTeamId as string)
        : undefined;
    const teamId = (cookieTeam || tokenTeam || "").trim();

    if (!teamId) {
      console.info("[middleware] deny", {
        reason: "missing-team",
        pathname,
        role,
        requiresTeam,
        userId: token.sub,
      });
      if (isAPI) {
        return new NextResponse(JSON.stringify({ error: "Team selection required" }), {
          status: 428,
          headers: { "content-type": "application/json" },
        });
      }
      const url = req.nextUrl.clone();
      url.pathname = "/redirect";
      url.searchParams.set("team", "select");
      return NextResponse.redirect(url);
    }

    const requestHeaders = new Headers(req.headers);
    requestHeaders.set("x-team", teamId);

    return NextResponse.next({
      request: {
        headers: requestHeaders,
      },
    });
  }

  return NextResponse.next();
}

export const config = {
  matcher: [
    // Home y login quedan pblicos; protegemos resto
    "/ct/:path*",
    "/api/ct/:path*",
    "/api/sessions/:path*",
    // "/api/users/:path*"  lo sacamos del matcher, as ni pasa por el middleware
    "/medico/:path*",
    "/api/medico/:path*",
    "/admin/:path*",
  ],
};
